<template>
	<div id="statcenter-location">
		<public-header title="定位信息"></public-header>
		<div class="content">
			<div class="location-list">
				<div class="location-item" v-for="(item, index) in locationInfoArr" :key="item.id">
					<div class="font-14 c-gray">添加时间: {{item.addtime_cn}}</div>
					<div class="font-14 c-gray padding-5-t">经度: {{item.location_x}}</div>
					<div class="font-14 c-gray padding-5-t">纬度: {{item.location_y}}</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				locationInfoArr: []
			}
		},
		methods: {
			onLoad() {
				var locationInfoArr = this.util.getStorage('locationInfoArr');
//				var temp = {
//					location_x: 11.126,
//					location_y: 11.126,
//					addtime: Date.parse(new Date()) / 1000,
//					addtime_cn: this.util.formatDate(new Date()),
//				};
//				locationInfoArr.push(temp);
				this.locationInfoArr = locationInfoArr;
				console.log(this.locationInfoArr);
//				this.util.setStorage('locationInfoArr', locationInfoArr);
			},
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#statcenter-location .location-item{
		padding: 10px 15px;
		background-color: #fff;
		margin-bottom: 10px;
	}
</style>


